const COMPLETE = 'areaChangeComplete';

/**
 *  bbq
 * @param {*} id 
 */
const existenceCheck = function (id) {
    let store = this.editTableDataSource[id]
    if (!store) {
        warningOnce(false, `ID${id}`);
        return;
    }
    return store;
}

/**
 * index bbq
 * @param {*} id 
 */
const indexCheck = function (id, index, rowid) {
    let store = existenceCheck.call(this, id);
    if (!store) { return; }
    let viewData = store.getData([ROW_STATUS.delete, ROW_STATUS.filter]);
    if (isNaN(index) || index > viewData.length || index < 0) {
        warningOnce(index, 'index0');
        return -1;
    }
    return index;
}

